<template>
  <div>
    <el-table
      :data="tableData"
      border>
      <el-table-column
        label="分类标题">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.label}}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="类型">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.value}}</span>
        </template>
      </el-table-column>

      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="edit(scope.row._id)">编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="delData(scope.row._id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination @pageSizeChange="pageSizeChange"  @currentPageChange="currentPageChange"></pagination>
  </div>
</template>

<script>
  import pagination from '@/components/pagination'

  export default {
    name: "news",
    components:{
      pagination
    },
    data() {
      return {
        tableData:[],
        params:{
          page: 1,
          pageSize: 5
        }


      }
    },
    methods:{
      getdata(){
        this.$axios.post('getcategory',this.params,{withCredentials: true}).then((res)=>{
          this.tableData = res.data
        })
      },
      delData(val){
        this.$confirm('此操作将永久删除该新闻分类, 是否继续?', '提示', {
          cancelButtonText: '取消',
          confirmButtonText: '确定',
          type: 'warning'
        }).then(()=>{
          this.$axios.post('delcategory',{id:val}).then((res)=>{
            console.log(res)
            if(res.code==200){
              this.$message({
                message: '新闻分类删除成功',
                type: 'success'
              });
              setTimeout(()=>{
                this.$router.go(0)
              },1000)
            }
          })
        }).catch(()=>{
          console.log(res.err)
        })

      },
      edit(id){
        this.$router.push({path:'/crmcategory/update',query:{id}})
      },
      pageSizeChange(val) {
        this.params.pageSize = val;
        this.getdata()
      },
      currentPageChange(val) {
        this.params.page = val;
        this.getdata()
      },
    },
    created(){
      this.getdata()
    }
  }
</script>

<style scoped>
  .img-item{
    width: 100px;
    max-height: 100px;
  }
</style>



